import React from 'react';
import Latex from 'react-latex';
import Divider from '@material-ui/core/Divider';

export default function Operacoes(){ return(<>

<h2>
    Operações com vetores e matrizes 
</h2>

<h3> Transposta </h3> 
a transposta de uma matriz <Latex>{"$A=a_{ij}$"}</Latex> é dada por <Latex>{"$A^{T} = a_{ji}$"}</Latex>.
<br /><br />
<b>Propriedades :</b>
<ul>
    <li>
        <Latex>{"$(AB)^T=B^TA^T$"}</Latex>
    </li>
</ul>

<Divider />

<h3> Inversa </h3> 
A inversa de uma matriz A é dada por <Latex>{"$AA^{-1} = \\mathbf{I}$"}</Latex>. Não é possivel inverter matrizes singulares.

<br /><br />
<b>Propriedades :</b>
<ul>
    <li>
        <Latex>{"$(A^T)^{-1}=(A^{-1})^{T}$"}</Latex>
    </li>
</ul>


</>)}